footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  z-index: -1;
  text-align: center; }

footer > div.container {
  position: relative;
  left: -50%; }

a,
a:hover,
a:focus {
  text-decoration: none; }

div.container > p {
  margin-bottom: 10px !important; }

@media screen and (max-width: 720px) {
  div.container > p {
    font-size: 12px; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
